.main {
  min-height: calc(100vh - 70px - 64px);
  margin: 5rem auto;
}

header,
footer {
  background-color: var(--bs-primary);
  padding: 20px 0;
  font-size: 25px;
}

section {
  margin: 50px 0;
}

.card-body,
.card {
  flex-grow: 1;
}

.buy-btn {
  margin-top: auto;
}

.cart {
  position: fixed;
  z-index: 1;
  right: 0;
  cursor: pointer;
}
.cart .cart-quantity {
  position: absolute;
  width: 15px;
  height: 15px;
  background-color: #c45252;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 2rem;
  border-radius: 50%;
  font-size: 10px;
  cursor: pointer;
}

.basket-list {
  position: fixed;
  opacity: 0;
  top: -200%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease;
  background-color: #fff;
  width: 50rem;
  max-width: 95%;
  -webkit-box-shadow: 0px 4px 83px 32px rgba(0, 0, 0, 0.63);
  -moz-box-shadow: 0px 4px 83px 32px rgba(0, 0, 0, 0.63);
  box-shadow: 0px 4px 83px 32px rgba(0, 0, 0, 0.63);
}
.basket-list .baket-list-item {
  max-height: 20rem;
  overflow-y: auto;
}
.basket-list .baket-list-item .item {
  border-bottom: 1px solid silver;
  height: 30px;
}
.basket-list .Total-block {
  padding-top: 0.5rem;
}
.basket-list .Total-block .btn-clear {
  width: 5rem;
  margin-right: 1rem;
}
.basket-list .close-backet {
  position: fixed;
  right: 0;
}

.active-basket {
  opacity: 1;
  top: 50%;
  transition: all 0.3s ease;
}

.quantity {
  margin: 0 10px;
}

.remove:hover {
  color: red;
}

@media (max-width: 700px) {
  .cart {
    right: 50px;
  }
}
@media (max-width: 600px) {
  .basket-list,
  .btn-clear,
  .btn-success,
  .backet-name {
    font-size: 14px;
  }
  .remove {
    font-size: 15px;
  }
}
@media (max-width: 450px) {
  .basket-list,
  .btn-clear,
  .btn-success,
  .backet-name {
    font-size: 10px;
  }
  .close-backet {
    right: 5px;
  }
  .remove {
    margin-top: -5px;
    font-size: 15px;
  }
}
.gone-alert {
  position: fixed;
  right: 10rem;
  top: 3rem !important;
  animation: fade-in 250ms ease-in-out;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(2rem);
  }
  to {
    opacity: 1;
    transform: translateY(0rem);
  }
}